<template>
  <div class="container">
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="编号" align="center" />
      <el-table-column prop="name" label="姓名" align="center" />
      <el-table-column prop="tel" label="电话" align="center" />
      <el-table-column prop="company" label="公司" align="center" />
      <el-table-column prop="city" label="城市" align="center" />
      <el-table-column prop="status_name" label="状态" align="center" />
      <el-table-column prop="uptime_date" label="时间" align="center" />
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      tableData: [],
      count: 0,
      page_curren: 1,
      loading: false,
    }
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    FetchList() {
      this.loading = true
      Fetch('/article/businesscoolist/search')
        .then(r => {
          this.loading = false
          this.tableData = r.data.list
          this.count = r.data.count
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    editItem(row) {
      this.$router.push('/business/edit/' + row.id)
    },
  },
}
</script>
